import Link from "next/link";

export default function ProjectComponent({ title, description, tech, url }) {
    
    const techTags = (tech || "").split(',').filter(t => t.trim() !== "").map(t => t.trim());

    return (
        <article className="w-full max-w-5xl bg-[var(--color-surface)]/40 backdrop-blur-md border border-[var(--color-border)] rounded-3xl overflow-hidden flex flex-col lg:flex-row shadow-2xl hover:shadow-[var(--color-primary)]/10 transition-all duration-300 group h-auto lg:h-[500px]">

            {/* 1. ZONA DE IMAGEN (Placeholder) */}
            {/* Cuando tengas imágenes, reemplaza el contenido de este div por <Image ... /> */}
            <div className="relative w-full lg:w-5/12 h-64 lg:h-full bg-[var(--color-surface)] border-b lg:border-b-0 lg:border-r border-[var(--color-border)] flex items-center justify-center overflow-hidden group-hover:border-[var(--color-primary)]/30 transition-colors">

                {/* Fondo animado para el placeholder */}
                <div className="absolute inset-0 bg-gradient-to-br from-[var(--color-primary)]/5 to-transparent opacity-50"></div>

                {/* Icono temporal */}
                <div className="flex flex-col items-center gap-4 z-10 text-[var(--color-text-2)] group-hover:text-[var(--color-primary)] transition-colors">
                    <svg className="w-16 h-16 opacity-50" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={1.5} d="M4 16l4.586-4.586a2 2 0 012.828 0L16 16m-2-2l1.586-1.586a2 2 0 012.828 0L20 14m-6-6h.01M6 20h12a2 2 0 002-2V6a2 2 0 00-2-2H6a2 2 0 00-2 2v12a2 2 0 002 2z" />
                    </svg>
                    <span className="text-sm font-mono tracking-widest uppercase opacity-70">Preview Image</span>
                </div>
            </div>

            {/* 2. ZONA DE CONTENIDO */}
            <div className="w-full lg:w-7/12 p-8 lg:p-10 flex flex-col h-full relative">

                {/* Título y Enlace */}
                <div className="flex justify-between items-start gap-4 mb-4">
                    <h3 className="text-2xl lg:text-3xl font-bold text-[var(--color-foreground)] group-hover:text-[var(--color-primary)] transition-colors">
                        {title}
                    </h3>
                    <a
                        href={url}
                        target="_blank"
                        rel="noopener noreferrer"
                        className="p-2 rounded-full border border-[var(--color-border)] hover:bg-[var(--color-primary)] hover:text-white hover:border-[var(--color-primary)] transition-all duration-300"
                        title="Ver Repositorio"
                    >
                        <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M10 6H6a2 2 0 00-2 2v10a2 2 0 002 2h10a2 2 0 002-2v-4M14 4h6m0 0v6m0-6L10 14" /></svg>
                    </a>
                </div>

                {/* Etiquetas de Tecnología */}
                <div className="flex flex-wrap gap-2 mb-6">
                    {techTags.map((tag, i) => (
                        <span key={i} className="px-3 py-1 text-xs font-medium bg-[var(--color-primary)]/10 text-[var(--color-primary)] rounded-full border border-[var(--color-primary)]/20">
                            {tag}
                        </span>
                    ))}
                </div>

                {/* Descripción con scroll si es muy larga */}
                <div className="flex-grow overflow-y-auto pr-2 custom-scrollbar">
                    <p className="text-[var(--color-text-2)] leading-relaxed text-base lg:text-lg text-pretty">
                        {description}
                    </p>
                </div>

                {/* Decoración inferior */}
                <div className="absolute bottom-0 right-0 p-4 opacity-10 pointer-events-none">
                    <svg className="w-24 h-24 text-[var(--color-foreground)]" fill="none" stroke="currentColor" viewBox="0 0 24 24"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth={1} d="M10 20l4-16m4 4l4 4-4 4M6 16l-4-4 4-4" /></svg>
                </div>
            </div>
        </article>
    );
}
